import axios from 'axios';

export default ({
    actions: {
        savePhrase (ctx, state) {

            let active_skill = []
            ctx.getters.ListSkills.forEach(element => {
                if (element.active === true) {
                    active_skill.push(element.id)
                }
            });

            let data = JSON.stringify({
                action: 'savePhrase',
                valid: state.valid,
                id: ctx.getters.Phrase.id,
                skills: JSON.stringify(active_skill),
                ners: JSON.stringify(ctx.getters.Phrase.ners)
            });

            return new Promise((resolve, reject) => {
                axios.post("http://127.0.0.1:8000/api.py", data)
                .then((response) => {
                    resolve()
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        },
        nero_refresh (ctx, state) {

            let data = JSON.stringify({
                action: 'nero_refresh',
                id: state,
            });

            return new Promise((resolve, reject) => {
                axios.post("http://127.0.0.1:8000/api.py", data)
                .then((response) => {
                    resolve(response.data.phrase)
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        }
    },
})