import axios from 'axios';
import { log } from 'util';
import Vue from 'vue'

export default ({
    state: {
        Phrase: {
            id: null,
            text: '',
        }
    },
    getters: {
        Phrase: state => state.Phrase
    },
    actions: {
        getPhrase (ctx) {
            let data = JSON.stringify({
                action: 'getPhrase'
            });
            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("markToken", null)
                    ctx.commit("updatePhrase", response.data.phrase);
                    resolve(response.data.phrase.skills);
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        }
    },
    mutations: {
        updatePhrase (state, data) {
            state.Phrase = data
        },
        updateNer (state, data) {
            for (var i=data.start; i<=data.stop; i++) {
                if (data.type === 'O') {
                    Vue.set(state.Phrase.ners[1], i, data.type)
                } else {
                    if (i === data.start) {
                        Vue.set(state.Phrase.ners[1], i, 'B-'+data.type)
                    } else {
                        Vue.set(state.Phrase.ners[1], i, 'I-'+data.type)
                    }
                }
            }
            let check = true
            let last = data.stop
            while (check) {
                if (state.Phrase.ners[1].length > last) {
                    last += 1
                    if (state.Phrase.ners[1][last] === 'O' || state.Phrase.ners[1][last].startsWith('B-')){
                        check = false
                    } else {
                        Vue.set(state.Phrase.ners[1], last, 'O')
                    }
                }
            }
        }
    }
})