<template>
    <v-layout row>
        <v-flex xl4 offset-xl4 lg6 offset-lg3 md8 offset-md2 sm10 offset-sm1 xs12 mb-3>
            <v-card>
                <v-card-actions>
                    <v-btn :color="'grey'" fab dark @click="PrevEl" v-bind:disabled="Phrase.id===1 ? true : false"><v-icon>chevron_left</v-icon></v-btn>
                    <v-spacer></v-spacer>
                    <v-text-field mr-3 v-model="phrase_id" @keyup.enter="OpenEl"></v-text-field>
                    <v-spacer></v-spacer>
                    <v-btn :color="'grey'" fab dark @click="NextEl" v-bind:disabled="Phrase.id===Phrase.last_phrase ? true : false"><v-icon>chevron_right</v-icon></v-btn>
                </v-card-actions>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
    name: 'NumberPhrase',
    data() {
        return {
            phrase_id: ''
        };
    },
    computed: {
        ...mapGetters(["Phrase", "ListSkills"])
    },
    watch: {
        Phrase (n,o){
            this.phrase_id = n.id
        }
    },
    methods: {
        PrevEl(){
            this.$store.dispatch('PrevEl', this.phrase_id)
            .then(phrase => this.updateEl(phrase))
        },
        NextEl(){
            this.$store.dispatch('NextEl', this.phrase_id)
            .then(phrase => this.updateEl(phrase))
        },
        OpenEl(){
            this.$store.dispatch('OpenEl', this.phrase_id)
            .then(phrase => this.updateEl(phrase))
        },
        updateEl(phrase){
            this.$store.commit("updatePhrase", phrase);
            this.$store.commit("markFalseActiveListSkills");
            this.$store.commit("markActiveListSkills", phrase.skills);
        }
    }
}
</script>
