import axios from 'axios';
import { log } from 'util';

export default ({
    state: {
        ListSkills: []
    },
    getters: {
        ListSkills: state => state.ListSkills
    },
    actions: {
        getListSkills (ctx) {
            let data = JSON.stringify({
                action: 'getListSkills'
            });
            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("updateListSkills", response.data.ListSkills);
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        },
        addSkill (ctx, dataskill) {
            let data = JSON.stringify({
                action: 'addSkill',
                dataskill: JSON.stringify(dataskill)
            });
            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("updateListSkills", response.data.ListSkills);
                    resolve()
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        },
    },
    mutations: {
        updateListSkills (state, data) {
            let newArr = []
            data.forEach(el => {
                el['active'] = false
                newArr.push(el)
            })

            newArr.sort((a,b) => b.count - a.count)

            state.ListSkills = newArr;
        },
        markFalseActiveListSkills(state) {
            state.ListSkills.forEach(x => x.active = false);
        },
        markActiveListSkills (state, skills) {
            skills.forEach(element => {
                state.ListSkills.find(x => x.id === element.id).active = true
            });
        },
    }
})