<template>
  <v-layout row>
    <v-flex>
      <v-card>
        <v-container>
          <span class="grey--text">Фраза</span>
          <p class="text--primary">{{ token }}</p>

          <v-layout row wrap>
            <v-autocomplete v-model="type_phrase" :items="ners" label="Тип" placeholder="Тип..."></v-autocomplete>

            <v-dialog v-model="dialog" width="500">
              <template v-slot:activator="{ on }">
                <v-btn icon v-on="on" color="red lighten-1" dark>
                  <v-icon>add</v-icon>
                </v-btn>
              </template>

              <v-card>
                <v-card-title class="headline grey lighten-2" primary-title>Добавление нового типа</v-card-title>

                <v-card-text>
                  <v-container grid-list-md>
                    <v-layout wrap>
                      <v-flex xs6>
                        <v-text-field label="Наименование*" required v-model="new_type.name"></v-text-field>
                      </v-flex>
                      <v-flex xs6>
                        <v-text-field label="Аббревиатура*" required v-model="new_type.abbr"></v-text-field>
                      </v-flex>
                      <v-flex xs12>
                        <v-text-field label="Описание" v-model="new_type.desc"></v-text-field>
                      </v-flex>
                      <v-flex xs12>
                        <v-text-field label="Сортировка" v-model="new_type.sort"></v-text-field>
                      </v-flex>
                      <v-flex xs6>
                        <v-text-field label="Цвет" required v-model="new_type.color"></v-text-field>
                      </v-flex>
                      <v-flex xs6>
                        <v-text-field
                          label="Образец"
                          style="pointer-events: none"
                          v-model="new_type.abbr"
                          :style="'background-color: '+new_type.color"
                        ></v-text-field>
                      </v-flex>
                      <!-- <v-color-picker v-model="new_type.color"></v-color-picker> -->
                    </v-layout>
                  </v-container>
                  <small>*Обязательно для заполнения</small>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" flat @click="dialog = false">Закрыть</v-btn>
                  <v-btn color="primary" flat @click="addNer">Добавить</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-layout>
        </v-container>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Ner",
  data() {
    return {
      token: "",
      type_phrase: null,
      dialog: false,
      new_type: {
        name: "",
        desk: "",
        abbr: "",
        color: "",
        sort: 500
      }
    };
  },
  computed: {
    ...mapGetters(["Ners", "start", "stop", "Phrase"]),
    ners() {
      return this.Ners.map(ner => ner.name);
    }
  },
  watch: {
    start(n, o) {
      this.getList();
      this.type_phrase = null;
    },
    stop(n, o) {
      this.getList();
      this.type_phrase = null;
    },
    type_phrase(n, o) {
      if (n && this.token != '') {
        let t = this.Ners.find(x => x.name === n);
        this.$store.commit("updateNer", {
          type: t.abbr,
          start: this.start,
          stop: this.stop
        });
      }
    },
    dialog(n, o) {
      if (n == false) {
        this.new_type = {
          name: "",
          desk: "",
          abbr: "",
          color: "",
          sort: 500
        };
      }
    }
  },
  methods: {
    getList() {
      let t = "";
      for (var i = this.start; i <= this.stop; i++) {
        t += " " + this.Phrase.ners[0][i];
      }
      this.token = t;
    },
    addNer() {
      this.$store
        .dispatch("addNer", this.new_type)
        .then(() => this.dialog = false);
    }
  },
  mounted() {
    this.$store.dispatch("getNers");
  }
};
</script>