<template>
  <v-card>
    <v-toolbar color="cyan" dark height="56">
      Скилы
      <v-spacer></v-spacer>

      <v-dialog v-model="dialog" width="500">
        <template v-slot:activator="{ on }">
          <v-btn icon v-on="on" color="red lighten-1" dark>
            <v-icon>add</v-icon>
          </v-btn>
        </template>

        <v-card>
          <v-card-title class="headline grey lighten-2" primary-title>Добавление нового скила</v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field label="Наименование*" required v-model="name"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field label="Описание" v-model="desc"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
            <small>*Обязательно для заполнения</small>
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" flat @click="dialogClose">Закрыть</v-btn>
            <v-btn color="primary" flat @click="addSkill">Добавить</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-list one-line :class="'listskills'">
      <template v-for="(item, index) in ListSkills">
              <v-list-tile :key="'skill-'+index">
                    <v-switch color="primary" v-model="item.active" :label="item.name"></v-switch>
                    <v-tooltip left>
                      <template v-slot:activator="{ on }">
                        <v-icon v-on="on" small>help</v-icon>
                      </template>
                      <span>{{item.desc}}</span>
                    </v-tooltip>
              </v-list-tile>
        <v-divider :key="index" :inset="item.inset"></v-divider>
      </template>
    </v-list>
  </v-card>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Skills",
  data() {
    return {
      dialog: false,
      name: "",
      desc: ""
    };
  },
  mounted() {
    this.$store.dispatch("getListSkills");
  },
  computed: {
    ...mapGetters(["ListSkills"])
  },
  methods: {
    dialogClose() {
      this.dialog = false;
      this.name = "";
      this.desc = "";
    },
    addSkill() {
      this.$store
        .dispatch("addSkill", { name: this.name, desc: this.desc })
        .then(() => {
          this.dialogClose();
        });
    }
  }
};
</script>

