<template>
  <v-layout>
    <v-flex xs12>
      <v-card>
        <v-card-title primary-title>
          <div>
            <div class="subheading" ref="target_str">
              <template v-for="(t, i) in Phrase.ners[0]">
                <!-- <div class="token"   > -->
                  <span class="token"
                        ref="target" 
                        :key="'t_'+i" 
                        :style="getColor(i)" 
                        @mousedown="pos[0]=i" 
                        @mouseup="mark_t(i)"
                        >
                    {{ t }}
                    <v-tooltip top>
                      <template v-slot:activator="{ on }">
                        <span class="tooltip" v-on="on">{{ getType(i) }}</span>
                      </template>
                      <span>{{ getDesc(i) }}</span>
                    </v-tooltip>
                  </span>
              </template>
            </div>

            <div class="clb"></div>

            <v-btn text small @click="expansion=!expansion">Полный текст</v-btn>
          </div>
        </v-card-title>

        <v-card-title primary-title v-if="expansion">
          <div>
            <div class="subheading grey--text">{{ Phrase.full_text }}</div>
            <span class="grey--text">Полный текст</span>
          </div>
        </v-card-title>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";
import { nextTick } from "q";

export default {
  name: "Phrase",
  data() {
    return {
      expansion: false,
      pos: [],
    };
  },
  computed: {
    ...mapGetters(["Phrase", "ListSkills", 'Ners']),
  },
  watch: {
    Phrase(n, o) {
      this.expansion = false;
    },
  },
  methods: {
    mark_t(i) {
      this.pos[1] = i
      this.pos.sort((a,b) => a-b)

      this.$store.commit("markToken", this.pos);
    },
    getType(i) {
      let ners = this.Phrase.ners[1];
      
      if (i == ners.length-1) {
        return this.getPrevType(i);
        } else {
          return this.getNextType(i);
      }
    },
    getNextType(i) {
      let ners = this.Phrase.ners[1];
      let c = ners[i];
      let n = ners[i+1];

      // Если текущее значение ОПРЕДЕЛЕНО
      // и текущее и следующее значение это НАЧАЛО то возвращаем значение типа
      if (c != 'O' && n.startsWith('B-')) {
        return c.replace('B-', '').replace('I-', '');
      }

      // Очищаем типы от не значащих символов
      c = c.replace('B-', '').replace('I-', '');
      n = n.replace('B-', '').replace('I-', '');

      // Если текущее значение не определено
      if (c === 'O') { 
        return ''
      } // Если текущий и следующий тип равны
      else if (c === n) { 
        return ''
      } 
      else {
        return c
      }
    },
    getPrevType(i) {
      let ners = this.Phrase.ners[1];
      
      let c = ners[i];

      // Если только одно слово
      if (ners.length == 1) {
        if (c != 'O') {
          return c.replace('B-', '');
        } else {
          return ''
        }
      } 

      let p = ners[i-1];

      // Если текущее значение ОПРЕДЕЛЕНО
      // и текущее и предыдущее значение это НАЧАЛО то возвращаем значение типа
      if (c != 'O' && (c.startsWith('B-') && p.startsWith('B-'))) {
        return c.replace('B-', '');
      }

      // Очищаем типы от не значащих символов
      c = c.replace('B-', '').replace('I-', '');
      p = p.replace('B-', '').replace('I-', '');

      // Если текущее значение не определено
      if (c === 'O') { 
        return ''
      } // Если не последнее слово 
        // и текущий и предыдущий тип равны
      else if (i != ners.length-1 && c === p) { 
        return ''
      } 
      else {
        return c
      }
    },
    getNer(type) {
      return this.Ners.find(x => x.abbr === type)
    },
    getColor(i) {
      
      let ners = this.Phrase.ners[1];
      let type = ners[i];

      if (type === 'O') {
        return 'background-color: #fff'
      }

      let t_socr = type.replace('B-', '').replace('I-', '')
      
      let ner = this.getNer(t_socr)
      
      if (ner.color != '') {
        return 'background-color: ' + ner.color
      } else {
        return 'background-color: #777'
      }
    },
    getDesc(i) {
      let ners = this.Phrase.ners[1];
      let type = ners[i];

      if (type === 'O') {
        return ''
      } else {
        return this.getNer(type.replace('B-', '').replace('I-', '')).desc
      }
    },
  },
  mounted() {
    this.$store.dispatch("getPhrase")
    .then(skills => {
      this.$store.commit("markFalseActiveListSkills");
      this.$store.commit("markActiveListSkills", skills);
    });
  }
};
</script>

<style>
.subheading {
  margin-bottom: 20px;
  display: inline-block;
}
.token {
  display: block;
  float: left;
  padding: 0 7px;
  /* margin: 0 1px; */
}
.tooltip {
  font-size: 10px;
  margin: 0 1px 0 0;
}
.clip {
  white-space: nowrap; /* Запрещаем перенос строк */
  overflow: hidden; /* Обрезаем все, что не помещается в область */
  text-overflow: ellipsis; /* Добавляем многоточие */
}
.clb {
  clear: both;
}
</style>
