<template>
  <v-layout row>
    <v-flex xs8 offset-xs2 mt-3>
      <v-card>
        <v-card-actions>
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn
                :color="valid == false ? 'error' : 'grey'"
                v-on="on"
                fab
                dark
                @click="valid = false"
                class="action"
              >
                <v-icon>thumb_down</v-icon>
              </v-btn>
            </template>
            <span>Не валидно</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn
                :color="valid == true ? 'success' : 'grey'"
                fab
                dark
                v-on="on"
                @click="valid = true"
              >
                <v-icon>thumb_up</v-icon>
              </v-btn>
            </template>
            <span>Валидно</span>
          </v-tooltip>

          <v-spacer></v-spacer>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark small v-on="on" @click="undo" class="action">
                <v-icon>undo</v-icon>
              </v-btn>
            </template>
            <span>Вернуть</span>
          </v-tooltip>

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn color="grey" fab dark small v-on="on" @click="nero_refresh">
                <v-icon>refresh</v-icon>
              </v-btn>
            </template>
            <span>Обновить NER</span>
          </v-tooltip>

          <v-spacer></v-spacer>
          <v-btn color="primary" dark @click="savePhrase">Далее</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Action",
  data() {
    return {
      valid: null
    };
  },
  computed: {
    ...mapGetters(["Phrase"])
  },
  watch: {
    Phrase(n, o) {
      if (n.valid === "True") {
        this.valid = true;
      } else if (n.valid === "False") {
        this.valid = false;
      } else {
        this.valid = null;
      }
    }
  },
  methods: {
    savePhrase() {
      if (this.valid !== null) {
        this.$store.dispatch("savePhrase", { valid: this.valid }).then(() => {
          this.valid = null;
          this.$store.dispatch("getPhrase");
          this.$store.dispatch("getListSkills");
        });
      }
    },
    undo() {
      this.$store.dispatch("OpenEl", this.Phrase.id).then(phrase => {
        this.$store.commit("updatePhrase", phrase);
        this.$store.commit("markFalseActiveListSkills");
        this.$store.commit("markActiveListSkills", phrase.skills);
      });
    },
    nero_refresh() {
      this.$store.dispatch("nero_refresh", this.Phrase.id).then(phrase => {
        this.$store.commit("updatePhrase", phrase);
        this.$store.commit("markFalseActiveListSkills");
        this.$store.commit("markActiveListSkills", phrase.skills);
      });
    }
  }
};
</script>

<style>
.action {
  margin-right: 10px !important;
}
</style>