import { log } from "util"

import axios from 'axios';

export default ({
    state: {
        Ners: [],
        start: null,
        stop: null
    },
    getters: {
        start: state => state.start,
        stop: state => state.stop,
        Ners: state => state.Ners
    },
    actions: {
        getNers (ctx) {
            let data = JSON.stringify({
                action: 'getNers'
            });
            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("updateNers", response.data.ners);
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        },
        addNer (ctx, state) {
            let data = JSON.stringify({
                action: 'addNer',
                ner: state
            });
            return new Promise((resolve, reject) => {
                axios.post(src+"/api.py", data)
                .then((response) => {
                    ctx.commit("updateNers", response.data.ners);
                    resolve()
                })
                .catch((error => {
                    console.log(error.statusText);
                }));
            });
        }
    },
    mutations: {
        updateNers (state, ners) {
            ners.sort((a,b) => a.order - b.order)
            state.Ners = ners
        },
        markToken (state, tokens) {
            if (tokens != undefined) {
                state.start = tokens[0]
                state.stop = tokens[1]
            }
        }
    }
})